
.main-title {
    display: none;
}


.contact__item {
  background: var(--white-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin-bottom: 20px;
  padding-top: 43px;
  padding-bottom: 43px;
  max-width: 220px;
  
}
.contact__title {
  font-size: 12px;
  line-height: 186%;
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--white-color);
}
.contact__secondary-title {
    font-weight: 500;
font-size: 12px;
line-height: 186%;
letter-spacing: 0.04em;
padding-left: 20px;
margin-top: 25px;
}
.contact__title-bg {
  background: var(--contacts-title-bg);
  border-radius: 4px;
  width: 80px;
  margin-left: 20px;
  
}

.contact__title-bg-foodtruck {
  background: var(--contacts-title-bg-second);
  border-radius: 4px;
  width: 130px;
  margin-left: 20px;
}

.contact__description {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: var(--grey-color);
  padding: 10px 21px 25px 20px;
  position: relative;

     &::after {
          content: "";
    border: 1px solid var(--gradient-bars-color);
    width: 179px;
    position: absolute;
    left: 20px;
    bottom: 0px;
     }
}

.time-table__item {
    margin-bottom: 10px;
    margin-top: 25px;
}
.time-table__day {
    
font-weight: 500;
font-size: 12px;
line-height: 186%;
color: var(--main-color);
padding-left: 20px;
position: relative;

}

.time-table__hours {
    font-weight: 500;
font-size: 12px;
line-height: 186%;
color: var(--time-table-color);
padding-left: 20px;


}
.bottom-list__link {
    font-weight: 500;
font-size: 12px;
line-height: 18px;
color: var(--grey-color);
text-decoration: none;
padding-left: 20px;
}
.contact__bottom-list {
    margin-top: 50px;
    position: relative;
    &::before {
    position: absolute;
    content: "";
    border: 1px solid var(--gradient-bars-color);
    width: 179px;
    left: 20px;
    bottom: 65px;
}
}
.contact__button-list {
    display: flex;
    flex-direction: column;
    margin-top: 54px;
    justify-content: center;
    
}

.contact__button-locations {
    font-weight: bold;
font-size: 16px;
line-height: 21px;
display: flex;
justify-content: center;
text-align: center;
color: var(--white-color);
border: none;
background: var(--contact-button-red-bg);
border-radius: 22px;
padding-left: 17px;
padding-right: 20px;
padding-top: 10px;
padding-bottom: 10px;

}
.contact__button-franchise {
   font-size: 16px;
line-height: 21px;
display: flex;
align-items: center;
text-align: center;

color: var(--secondary-color);
    border: none;
background: var(--white-color);
border-radius: 22px;
padding-left: 32px;
padding-right: 48px;
padding-top: 10px;
padding-bottom: 10px;
margin-top: 10px;
}
.contact__button-icon {
  width: 10px;
  height: 10px;
  margin-left: 10px;
  margin-top: 7px;
}
.contact__button-icon-red {
  width: 10px;
  height: 10px;
  margin-left: 10px;
  margin-top: 3px;
}


@media screen and (min-width:768px) {

  .container {
  max-width: 768px;
  
}
  .contact__list {
    display: flex;
    
  }
  .contact__item {
    margin-right: 20px;
  }
  .contact__item-last {
    margin-right: 0px;
  }
  .contact__button-list {
    flex-direction: row;
    align-items: center;
  }
  .contact__button-franchise {
    margin-left: 15px;
    margin-top: 0px;
  }

}